  <!--<Footer-component @footer-click="clearAll"></Footer-component>-->
  <!--</div>-->
  <router-view></router-view>
</template>
<script>
  import store from 'store'
  import axios from 'axios'
  import FooterComponent from '../components/Footer'
  import HeaderComponent from '../components/Header'
  import TabList from '../components/TabList'
  export default {
    name: 'appindex',
    data() {
      return {
        tabs: {},  // tab
        activeTab: 0,   // tab
        searchList: [],   // 
        limit: 10,   // 
        keyWord: '',  // 
        searchStatus: false,   // 
        tabArr: ['', '', '', '', '']
      }
    },
    components: {
      FooterComponent,
      HeaderComponent,
      TabList
    },
    methods: {
      // 
      fetchList($index) {
        let q = store.get('indextab' + $index)
        if (q) {
          this.$router.push({
            path: '/tab/' + $index,
            query: {
              scrollX: q.scrollX,
              scrollY: q.scrollY
            }
          })
        } else {
          this.$router.push({
            path: '/tab/' + $index
          })
        }

        this.activeTab = $index
      },
      // 
      handleSearch() {
        axios.get('/api/customer/query?type=SEARCH&keywork=' + this.keyWord).then((res) => {
          this.listData.splice(this.activeTab, 1, res.data)
        })
      },
      handleFocus () {
        this.$router.push('/search')
      },
      clearAll () {
        store.clearAll()
      }
    },
    created() {
      this.fetchList(this.activeTab)   // 
    }
  }
</script>





